// tooltip
$(document).ready(function () {
    $('[data-toggle="tooltip"]').tooltip({boundary: 'window'});
});


var section1Btn = document.getElementById("section1Btn");
section1Btn.onclick = function () {
    TweenLite.to(window, 1, {scrollTo: {y: "#section1", offsetY: 70, autoKill: false}});
};


var privat = document.getElementById("private"),
    business = document.getElementById("business"),
    preview = document.getElementById("preview"),
    blockDetailsText = document.getElementById("block-details-text"),
    previewDetails = document.getElementById("preview-details"),
    privateOpenImages = document.getElementById("private-open-images"),
    businessOpenImages = document.getElementById("business-open-images");


privat.onclick = function () {
    pri()
}

function pri() {
    let tl = new TimelineMax({
        onStart: console.log('play'),
        onComplete: console.log('finish'),
    });

    tl
        .to('#private', 0.5, {
            scale: 0.5,
            x: 300,
            alpha: 0,
            display: 'none',
            ease: Power1.easeOut,
        }, 0)
        .to(['#business'], 0.5, {
            opacity: 0,
            alpha: 1,
            display: 'none',
            ease: Power1.easeOut,
        }, 0)
        .to('#preview', 1, {
            alpha: 0,
            display: 'none',
            ease: Power1.easeOut,
        }, 2)
        .to('#preview-details', 1, {
            display: 'flex',
            alpha: 1,
            ease: Power4.easeIn,
        }, 1)
        .to(['#block-details-text ', '.card-text2'], 1, {
            display: 'block',
            alpha: 1,
            ease: Power4.easeIn,
        }, 1)
        .to('#private-open-images', 1, {
            display: 'block',
            alpha: 1,
            ease: Power4.easeIn,
        }, 2);
    return tl;
}

function numberSet(num) {
    let roundNumber = document.getElementById('SVGText')
    roundNumber.textContent = num.slice(5);
}

//point1
$('.round1').on('click', function (e) {
    let tl = new TimelineMax({
        onStart: roundWhite,
        onComplete: numberSet(e.currentTarget.className.baseVal)
    });
    tl.to(['.round1', '.point1'], 0.1, {
        fill: '#F7B443',
    }, 0.1)
    tl.to(['.point1'], 0.1, {
        fill: '#F7B443',
        repeat: 4,
        yoyo: true
    }, 0.1)
        .to('.round1-text', 0.1, {
            fill: '#fff'
        }, 0.1)
        .to('.card-text1', 1, {
            scale: 1,
            autoAlpha: 1,
            display: 'block',
            clearProps:"scale,autoAlpha",
        }, 0);

    return tl;
});
