function roundWhite() {
    let tl = new TimelineMax({
        onStart: console.log('play'),
        onComplete: console.log('finish'),
    });
    tl.to(['.round1', '.round2', '.round3', '.round4', '.round5', '.round6', '.round7'], 0, {
        fill: '#fff'
    }, 0)
        .to(['.round1-text', '.point1', '.round2-text', '.point2', '.round3-text', '.point3', '.round4-text', '.point4', '.round5-text', '.point5', '.round6-text', '.point6', '.round7-text', '.point7'], 0, {
            fill: '#4D5051'
        }, 0)
        .to(['.card-text'], 0, {
            scale: 0.5,
            autoAlpha: 1,
            display: 'none',
        }, 0);

    return tl;
}


